.cart-icon{
    width:45px;
    height: 45px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    .shopping-icon{
        width:24px;
        height:24px;
    }

    .item-count{
        position:absolute;
        font-size:10px;
        font-weight: bold;
        bottom:12px;
    }
}