.menu-item {
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;
    overflow: hidden;
    
    
  
    &:first-child {
      margin-right: 7.5px;
    }
  
    &:last-child {
      margin-left: 7.5px;
    }

    & .large{
        height: 380px;
    }
    .background-image{
        background-size: cover;
        background-position: center;
        width: 100%;
        height: 100%;
        transition: transform 3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    }
    &:hover{
        cursor: pointer;
        .background-image{
            transform: scale(1.2);
            // transition: transform 6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
        }
        .content{
            opacity:1;
            // transition: opacity 1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
        }
    }
  
    .content {
      height: 90px;
      padding: 0 25px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid black;
      background:white;
      opacity: 0.7;
      position: absolute;
      transition: opacity 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  
      .heading {
        font-weight: bold;
        margin-bottom: 6px;
        font-size: 22px;
        color: #4a4a4a;
      }
  
      .sub-heading {
        font-weight: lighter;
        font-size: 16px;
      }
    }
  }